
@font-face {font-family: "iconfont";
  src: url(data:application/vnd.ms-fontobject;base64,); /* IE9*/
  src: url(data:application/vnd.ms-fontobject;base64,#iefix) format('embedded-opentype'), 
  url('data:application/x-font-woff;charset=utf-8;base64,') format('woff'),
  url(data:application/x-font-ttf;base64,) format('truetype'), 
  url(data:image/svg+xml;base64,#iconfont) format('svg'); /* iOS 4.1- */
}
[class*=" s-icon-"],
[class^="s-icon-"] {
  font-family: "iconfont" !important;
  font-size: inherit;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.s-icon-window-restore:before { content: "\EBA1";
}
.s-icon-message:before { content: "\E6F6";
}
.s-icon-windows:before { content: "\E86F";
}
.s-icon-window-close:before { content: "\E93B";
}
.s-icon-window-max:before { content: "\E93C";
}
.s-icon-min:before { content: "\E63C";
}
* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Microsoft YaHei", 微软雅黑, "MicrosoftJhengHei", 华文细黑;
}
html {
  height: 100%;
}
body {
  height: 100%;
  overflow: hidden;
}
#win10 {
  width: 100%;
  height: 100%;
  background: black url(/static/img/AerialPantanal.6c732a3.jpg) no-repeat fixed;
  background-size: 100% 100%;
  position: relative;
  padding-bottom: 40px;
}
.win10-shortcut {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  height: 100%;
  width: 80px;
}

.win10-menu {
  width: 680px;
  height: 480px;
  position: fixed;
  bottom: -480px;
  left: 0;
  color: white;
  background-color: rgba(19, 23, 28, 0.8);
  overflow-x: hidden;
  overflow-y: auto;
  z-index: 8;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

.win10-message {
  width: 360px;
  height: calc(100% - 40px);
  position: fixed;
  right: -360px;
  top: 0;
  color: white;
  background-color: rgba(19, 23, 28, 0.8);
  overflow-x: hidden;
  overflow-y: auto;
  z-index: 8;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.win10-message-header {
  height: 60px;
  line-height: 54px;
  font-size: 18px;
  font-weight: bolder;
  position: relative;
  padding: 6px;
}
.win10-message-header span {
  position: absolute;
  right: 6px;
  bottom: 6px;
  font-size: 14px;
  color: #808080;
  line-height: 1;
}
.win10-message-header span:hover {
  color: #fff;
  cursor: pointer;
}
.win10-message-content {
  font-weight: normal;
  color: #666666;
  line-height: normal;
}

.messageListItem {
  position: relative;
  left: 1px;
  margin: 10px 0;
  padding: 10px 7px;
  font-size: 12px;
  -webkit-transition: all 1s;
  transition: all 1s;
}
.messageListItem:hover {
  color: #fff;
  background-color:rgba(102,102,102,0.2);
}
.messageListHeader {
  margin-bottom: 5px;
  font-size: 15px;
}
.messageListContent {
}
.closeMessageListItem {
  opacity: 0;
  cursor: pointer;
  color: rgb(102,102,102);
  padding: 3px;
  font-size: 15px;
  line-height: 15px;
  font-weight: 900;
  position: absolute;
  right: 11px;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.closeMessageListItem:hover {
  color: #fff;
}
.messageListItem:hover .closeMessageListItem {
  opacity: 1;
}
.list-item {
  display: inline-block;
}
.list-enter-active, .list-leave-active {
  -webkit-transition: all 1s;
  transition: all 1s;
}
.list-enter, .list-leave-to
  /* .list-leave-active for below version 2.1.8 */ {
  opacity: 0;
  -webkit-transform: translateX(360px);
          transform: translateX(360px);
}

.win10-shortcut-item {
  width: 80px;
  overflow: hidden;
  cursor: pointer;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.win10-shortcut-item:hover {
  background-color: rgba(255, 255, 255, 0.3);
}
.win10-shortcut-item img {
  width: 50px;
  height: 50px;
  margin: 5px 15px;
  display: block;
}
.win10-shortcut-item div {
  color: #fff;
  font-size: 12px;
  text-align: center;
  line-height: 18px;
  margin-bottom: 5px;
}

.win10-taskbar {
  width: 100%;
  height: 40px;
  position: fixed;
  bottom: 0;
  background-color: rgba(19, 23, 28, 0.9);
  z-index: 999;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex
}
.win10-taskbar .win10-button {
  float: left;
}
.win10-taskbar-group-middle {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.win10-taskbar-time {
  width: auto;
  height: 40px;
  font-size: 14px;
  line-height: 20px;
  color: #fff;
  text-align: center;
  padding: 0 10px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  float: left;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.win10-taskbar-time:hover {
  background-color: rgba(106, 105, 100, 0.7);
}
.win10-taskbar-show-desktop {
  float: left;
  width: 10px;
  height: 40px;
  margin-left: 6px;
  border-left: 1px solid #808080;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.win10-taskbar-show-desktop:hover {
  background-color: rgba(106, 105, 100, 0.7);
  cursor: pointer;
}

.win10-button {
  width: 40px;
  height: 40px;
  line-height: 40px;
  font-size: 20px;
  color: white;
  text-align: center;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.win10-button:hover {
  background-color: rgba(106, 105, 100, 0.7);
  cursor: pointer;
}

.win10-window {
  background-color: #795da3;
  position: absolute;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.win10-window-header {
  height: 40px;
  line-height: 40px;
  background-color: rgba(49, 49, 50, 0.9);
  color: #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.win10-window-title {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  padding-left: 10px;
  cursor: move;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.win10-window-header-right .win10-button {
  float: left;
}
.win10-window-body {
  background-color: #fff;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.win10-window-top-left {
  position: absolute;
  left: -8px;
  top: -8px;
  width: 16px;
  height: 16px;
  cursor: nwse-resize;
}
.win10-window-top {
  position: absolute;
  left: 8px;
  right: 8px;
  height: 16px;
  top: -8px;
  cursor: ns-resize;
}
.win10-window-top-right {
  position: absolute;
  right: -8px;
  top: -8px;
  height: 16px;
  width: 16px;
  cursor: nesw-resize;
}
.win10-window-right {
  position: absolute;
  right: -8px;
  top: 8px;
  bottom: 8px;
  width: 16px;
  cursor: ew-resize;
}
.win10-window-bottom-left {
  position: absolute;
  left: -8px;
  bottom: -8px;
  width: 16px;
  height: 16px;
  cursor: nesw-resize;
}
.win10-window-bottom {
  position: absolute;
  right: 8px;
  left: 8px;
  height: 16px;
  bottom: -8px;
  cursor: ns-resize;
}
.win10-window-bottom-right {
  position: absolute;
  right: -8px;
  bottom: -8px;
  width: 16px;
  height: 16px;
  cursor: nwse-resize;
}
.win10-window-left {
  position: absolute;
  left: -8px;
  bottom: 8px;
  width: 16px;
  top: 8px;
  cursor: ew-resize;
}
